import React, { useState, useEffect, useCallback } from "react";
import { useNavigate } from "react-router-dom";
import { supabase } from "../supabaseClient";
import VideoCard from "./VideoCard";

const VideoList = ({ refreshTrigger, onVideosUpdate }) => {
  const [videos, setVideos] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const navigate = useNavigate();

  const fetchVideos = useCallback(async () => {
    try {
      setLoading(true);
      const { data, error } = await supabase.storage.from("video-clips").list();

      if (error) {
        throw error;
      }

      // 
      const videosWithUrls = await Promise.all(
        data
          .filter((d) => !d.name.includes(".emptyFolderPlaceholder"))
          .map(async (video) => {
            const {
              data: { publicUrl },
            } = supabase.storage.from("video-clips").getPublicUrl(video.name);

            // 
            const timestamp = parseInt(video.name.split("_")[0]);
            const uploadedAt = new Date(timestamp);

            return {
              ...video,
              publicUrl,
              uploadedAt,
              timestamp, // 
            };
          })
      );

      // 
      const sortedVideos = videosWithUrls.sort(
        (a, b) => b.timestamp - a.timestamp
      );
      setVideos(sortedVideos);

      //  videos 
      if (onVideosUpdate) {
        onVideosUpdate(sortedVideos);
      }
    } catch (err) {
      setError(err.message);
      console.error("", err);
    } finally {
      setLoading(false);
    }
  }, [onVideosUpdate]);

  useEffect(() => {
    fetchVideos();
  }, [fetchVideos, refreshTrigger]);

  const handlePlayVideo = (video) => {
    //  localStorage 
    localStorage.setItem(
      "currentVideo",
      JSON.stringify({
        url: video.publicUrl,
        name: video.name.split("_").slice(1).join("_"),
      })
    );
    //  React Router 
    navigate("/player");
  };

  if (loading) {
    return (
      <div className="flex justify-center items-center p-8">
        <p className="text-secondary">...</p>
      </div>
    );
  }

  if (error) {
    return (
      <div className="p-4 bg-red-100 text-red-700 rounded-md">
        <p>{error}</p>
      </div>
    );
  }

  return (
    <div className="space-y-4">
      <div className="flex justify-between items-center">
        <h2 className="text-xl font-bold text-secondary"></h2>
        <button
          onClick={fetchVideos}
          className="px-3 py-1 text-sm bg-primary-dark text-primary-light rounded-md hover:bg-primary transition-colors"
        >
          
        </button>
      </div>

      {videos.length === 0 ? (
        <p className="text-secondary-light text-center py-8"></p>
      ) : (
        <div className="grid grid-cols-2 gap-4">
          {videos.map((video) => (
            <VideoCard
              key={video.name}
              video={video}
              onPlay={handlePlayVideo}
            />
          ))}
        </div>
      )}
    </div>
  );
};

export default VideoList;
